/* eslint-disable react/prop-types */
import React from 'react';
import { ActivityIndicator } from 'react-native';
import { colors } from '@components/typo';
import './index.css';

export default function Button({ title, loading, ...props }) {
    return (
        <button className="button" {...props} disabled={loading}>
            {loading && <ActivityIndicator color="#fff" size={18} />}
            <div> {loading ? 'Please Wait...' : title}</div>
        </button>
    );
}
